// Study Resources Text
const StudyResource = [
  "<div>" +
  "<p>După căsătorie este prea târziu ca cineva să decidă dacă preferă să trăiască o viaţă de celibat sau nu. Apostolul arată foarte clar acest lucru, declarând că nici soţul nu are singur stăpânire asupra trupului său, nici soţia nu are singură stăpânire asupra trupului său, ci în căsătorie fiecare s-a predat celuilalt în astfel de grad încât orice refuzare a drepturilor conjugale moderate, rezonabile, ar însemna o nedreptate şi o încălcare a contractului de căsătorie. Apostolul vorbeşte despre un astfel de curs spunând: „să nu vă lipsiţi unul pe celălalt\" (1 Cor. 7:5). Timpul considerării unei astfel de chestiuni este înainte de căsătorie. N-ar fi cuvenit nici ca unul să încerce să-l oblige pe celălalt, nici ca amândoi să se angajeze la o viaţă de celibat în lanţurile căsătoriei. În aceasta, ca şi în oricare altă chestiune pământească, legea trebuie să fie moderaţia, frâna prin care Natura Nouă va căuta să-şi menţină controlul asupra cărnii* -- aducând până şi gândurile inimii în supunere faţă de Domnul. Reţinerea absolută, oricât ar fi de dorit, după cum arată apostolul, nu trebuie să fie impusă unul altuia, ca să nu devină o cursă şi o ispită spre încălcarea obligaţiilor conjugale. El spune:</p>" +
  "<p>„Soţul să dea soţiei ce-i datorează (ceea ce ea ar cere în mod raţional, natural şi drept), şi tot aşa şi soţia faţă de soţul ei. Soţia nu este stăpână (n-are stăpânire) pe trupul ei, ci soţul; tot astfel, nici soţul nu este stăpân (n-are stăpânire) pe trupul lui, ci soţia. Să nu vă lipsiţi unul pe celălalt decât doar prin bună învoială, pentru un timp, ca să vă dedicaţi rugăciunii; apoi să fiţi iarăşi împreună, ca să nu vă ispitească Satan, din cauza lipsei voastre de stăpânire. Lucrul acesta îl spun ca o îngăduinţă, nu ca o poruncă. Eu aş vrea ca toţi oamenii să fie ca mine (reţinut şi liber, practic un famen). . . . Celor necăsătoriţi şi văduvelor le spun că este bine pentru ei să rămână ca mine. Dar, dacă nu se pot înfrâna, să se căsătorească, pentru că este mai bine să se căsătorească decât să ardă\" -- să fie mistuiţi de o pasiune nestăpânită care le-ar împiedica părtăşia cu Domnul şi s-ar putea dovedi pentru ei o capcană. 1 Cor. 7:3-9.</p>" +
  "</div>",

  "<div>" +
  "<p style='text-align: center'>„Numai în Domnul\"\n</p>" +
  "<p>Ce raţionale, ce înţelepte sunt poruncile Domnului! -- ce folositoare sunt pentru cei care au ureche să le audă şi care se supun sfatului lor! Ca poporul Domnului să se căsătorească „numai în Domnul\", la început ar putea să pară o limitare, o constrângere, o robie: dar nu este aşa -- este numai un sfat. Oricine urmează sfatul va afla în cele din urmă că a fost binecuvântat prin aceasta, şi oricine nesocoteşte sfatul va afla în general mai târziu nechibzuinţa căii sale prin experienţe aspre.</p>" +
  "<p>Nici un alt contract sau aranjament aparţinător lucrurilor vieţii prezente nu este atât de important ca şi contractul de căsătorie: totuşi, oameni cu o minte destul de echilibrată par să-l trateze într-o manieră uşoară şi lipsită de seriozitate. Unii părinţi par să analizeze mai atent şi cu o judecată mai sănătoasă problema cumpărării unei ferme, a creşterii vitelor, oilor, cailor, câinilor şi porcilor, decât îşi analizează partea lor în propagarea speciei umane. Astfel de nechibzuinţă este greu de explicat, afară doar de presupunerea că ei privesc căsătoria ca un fel de loterie, condusă de întâmplare în loc să fie condusă de raţiune; sau că ei consideră pe Dumnezeu ca şi Creatorul fiecărui membru individual al neamului omenesc -- nediscernând că lucrarea perfectă a lui Dumnezeu în privinţa neamului nostru omenesc a fost îndeplinită în prima pereche, căreia El i-a dat puteri de reproducere care au trecut şi la urmaşii lor. Vederea corectă asupra naturii umane este aceea că ea e cel mai înalt tip al creaţiei animale, şi, asemenea restului creaţiei animale, ea a fost înzestrată de Creator cu putere de a se reproduce fiecare după specia ei proprie. Din acest punct de vedere, devine imediat evident că Dumnezeu nu este Creatorul direct al nici unuia din familia umană acum în viaţă şi că diferitele slăbiciuni, imperfecţiuni şi debilităţi mintale sub care suferă rasa nu pot fi puse pe seama imperfecţiunii lucrării Sale, ci pe seama căderii rasei noastre în păcat şi a acţiunilor naturale ale păcatului -- care înclină tot mai mult în jos spre imperfecţiune, degradare, moarte.</p>" +
  "</div>",

  "<div>" +
  "<p>Chiar bărbatul natural şi femeia naturală, deci, trebuie să se gândească atent la problema căsătoriei, aşa încât să-şi facă pe cât este posibil partea în compensarea influenţelor degradante care afectează rasa. Ei ar trebui să-şi dea seama, de exemplu, de necesitatea legilor consanguinităţii -- că mariajul celor cu legături de sânge apropiate ar trebui evitat. Astfel de reglementări n-au fost necesare la început, când fiii şi fiicele lui Adam s-au căsătorit între ei liber şi fără pericol, pentru că omenirea fiind încă aproape perfectă, aceasta nu le cauza copiilor anumite slăbiciuni; dar acum, fiindcă omenirea a devenit foarte coruptă şi deoarece nu numai bolile, ci şi caracteristicile mintale, trăsăturile şi sensibilităţile se transmit prin ereditate în familii, face parte din înţelepciune -- da, mai mult chiar, parte din datorie, din dreptate faţă de copiii pe care ei ar vrea să-i aducă în existenţă -- că ar trebui nu numai să evite legăturile de sânge apropiate, care ar putea intensifica particularităţile şi sensibilităţile mintale şi fizice, ci, în plus, ar trebui pe cât posibil să recunoscă faptul că este potrivit să-şi aleagă o pereche cu temperament diferit de al său. Natura pare să ajute într-o anumită măsură în această chestiune, aşa încât cei pur blonzi sau cei pur bruneţi în mod natural preferă de obicei o pereche cu o înfăţişare contrară.</p>" +
  "<p>Dar, deşi aceste reguli care aparţin omului natural s-ar aplica la Noua Creatură, dacă ar hotărî că în toate modurile ar fi mai înţelept să se căsătorească, mai există încă o prevenire a apostolului, ca să îndrume potrivirea Noii Creaţii după trup -- el ar trebui să aleagă „în Domnul\". Atunci ar fi potrivit atât în cele spirituale cât şi în cele naturale. Unii ar putea spune că dacă legătura apropiată după trup ar putea produce extreme la copii, atunci şi legătura apropiată după spirit s-ar putea dovedi dăunătoare -- să fie socotită că produce copii excentrici în privinţa chestiunilor morale şi religioase. Răspunsul nostru este, nu: căci în măsura în care se primeşte mintea cea nouă, influenţa ei este contrară excentricităţilor trupului. Apostolul declară: „Noi avem gândul lui Hristos\", „un duh ... de chibzuinţă\" -- noi vedem aceste lucruri din punctul de vedere al lui Cristos. Noua Creaţie este concepută de spiritul Său, chiar dacă este încă imperfectă după trup; şi este îndrumată de Spirit prin Cuvânt în înţelegerea gândului divin în toate lucrurile.</p>" +
  "</div>",

  "<div>" +
  "<p>Este adevărat, această minte nouă trebuie să lucreze în corpul muritor şi cu aparatul lui de gândire imperfect; totuşi, deşi imperfecţiunile cărnii ar putea într-o măsură colora mintea nouă şi i-ar putea deforma marea şi frumoasa simetrie, voinţa este superioară, iar carnea este mai mult sau mai puţin influenţată, formată, îndrumată şi transformată treptat de ea, aşa încât, în măsura în care unul va primi mintea lui Cristos, în aceeaşi măsură va deveni, în mod sigur, tot mai chibzuit în gândirea sa asupra tuturor problemelor şi intereselor vieţii. Aceasta nu înseamnă că el va fi apreciat de lume ca fiind mai înţelept decât înainte, ci înseamnă că va fi într-adevăr mai înţelept, şi neputinţa lumii de a discerne înţelepciunea sa crescândă va fi din cauză că lumea este oarbă, este neînţeleaptă, n-are mintea lui Cristos şi în general vede lucrurile dintr-un punct de vedere deformat, al depravării şi egoismului. Înţelepciunea pe care o dorim noi este aceea care vine de sus, care, după explicaţia apostolului, ne va face mai curaţi, mai paşnici, mai îndurători, mai blânzi faţă de fraţi, faţă de familie, faţă de omenire în general -- da, şi faţă de creaţia animală. Înţelepciunea acestei lumi, după cum explică apostolul, este pământească, senzuală, drăcească. Nu înseamnă că toţi bărbaţii şi toate femeile lumeşti sunt senzuali şi drăceşti, ci tendinţa generală a înţelepciunii lumeşti este în această direcţie; iar omenirea, oricât de orbită ar fi, se străduieşte împotriva legilor egoismului cu care este legată, deşi oamenii caută continuu să ascundă de ei şi de alţii lanţurile robiei lor.</p>" +
  "</div>",

  "<div>" +
  "<p>„În Domnul\" ar trebui înţeles că înseamnă cu mult mai mult decât doar o credinţă nominală în Domnul, cu mult mai mult decât doar a fi un membru în biserica nominală. Oamenii din lume ar trebui să se căsătorească cu oameni din lume; oamenii din biserica nominală ar trebui să se căsătorească cu oameni din biserica nominală; cei care doar cred, care se încred în sângele merituos al lui Cristos, ar trebui să ia în căsătorie tovarăşi asemănători. Dar cei care au făcut pasul deplinei consacrări şi au devenit membri ai corpului lui Cristos, ai Noii Creaţii, concepuţi din nou, ar trebui să se căsătorească numai cu cei de felul lor -- numai cu Creaturi Noi -- numai cu cei care sunt „în Domnul\" ca membri acceptaţi ai corpului lui Cristos, părtaşi ai spiritului Său de sfinţenie; şi, pe lângă aceasta, după cum s-a arătat deja, fiecare trebuie să caute să menţină relaţia cuvenită privitoare la sexe. Femeia trebuie să caute să se căsătorească „în Domnul\" numai cu un astfel de bărbat pe care-l poate privi ca şi cap al familiei, moral, intelectual, spiritual -- unul pe care ea îl poate „respecta\". Bărbatul trebuie să caute să se căsătorească „în Domnul\" cu una care să fie, după cât poate el judeca, un adevărat ajutor, cu minte curată, iubitoare, blândă, de ajutor -- nu una superioară lui, spre care în mod natural ar fi obligat să privească şi s-o aprecieze ca şi capul cuvenit al familiei. Toate aceste reguli de căsătorie ar trebui respectate de către Creaţia Nouă, care posedă spiritul unei minţi sănătoase, oricât de mult ar fi ele nesocotite de oamenii din lume, care nu sunt conduşi de mintea Domnului, ci sunt dispuşi să meargă pe propria lor cale, să fie conduşi de propriile lor capricii sau fantezii, sau de înşelarea unuia de către altul. Dacă se ridică îndoieli, să fie rezolvate pe partea sigură -- să aştepte până se rezolvă.</p>" +
  "</div>",

  "<div>" +
  "<p>S-ar putea obiecta că dacă mariajul ar fi privit dintr-un punct de vedere atât de deosebit, ar fi mai puţine căsătorii. Noi răspundem că se poate să fie aşa, dar că o mare proporţie a celor care sunt acum căsătoriţi, în special a celor care au ajuns prin harul Domnului la o înţelegere mai clară a relaţiei lor cu Domnul ca Noi Creaturi, şi la o cunoaştere a sfatului Său în interesul dezvoltării lor spirituale în chestiuni aparţinătoare cărnii, nu s-ar mai căsători aşa cum s-au căsătorit -- ei sunt mai înţelepţi acum. Conform curţilor de justiţie şi uzanţelor, posibilitatea unui divorţ complet şi a unei noi căsătorii pare să-i facă pe mulţi din lume mai puţin pretenţioşi, mai puţin grijulii în privinţa găsirii unei perechi. Dar Noua Creatură ar trebui să nu uite că pentru ea contractul de căsătorie este asemănător cu cel dintre Domnul şi Biserică -- continuu; că nu este anulat de nici o curte de justiţie pământească în aşa măsură încât să-i permită căsătoria cu altul, cu excepţia singurului caz specificat (Mat. 19:9). Pentru poporul Domnului căsătoria „în Domnul\" este de aceea un contract care atârnă greu şi ar trebui întreprins numai după analizare atentă, cu rugăciune şi cu examinarea fiecărui aspect legat de situaţie în măsura în care acestea pot fi discernute.</p>" +
  "</div>",

  "<div>" +
  "<p>Noua Creaţie mai are încă o protecţie în această problemă. Conform legământului lor cu Domnul, ei şi-au predat propria voinţă şi au acceptat în locul ei voinţa Capului, a Domnului; şi dacă aceasta este atitudinea minţii lor -- o dorinţă sinceră de a cunoaşte voinţa lui Cristos (1) cu privire la întrebarea dacă să se căsătorească sau nu, şi (2) cu privire la alegerea Domnului pentru ei, aceştia, după exercitarea celei mai bune judecăţi şi înţelepciuni a lor, vor încredinţa Domnului întreaga chestiune şi se vor ruga pentru conducerea lucrurilor conform înţelepciunii Sale, cu mulţumire bazându-şi inima pe conducerea ulterioară a providenţei divine oricare va fi ea -- fie că va fi favorabilă fie că va fi contrară cu ceea ce aprobase judecata lor cea mai bună. În acest fel şi nu în alt fel poporul Domnului poate fi sigur că merge pe o cale corectă. Având în vedere cele precedente, ce important este ca Noua Creaţie să aibă clar în minte instrucţiunile Cuvântului Domnului asupra acestui subiect; să aibă însuşi spiritul Adevărului şi să ţină minte continuu faptul că sunt Creaturi Noi -- că nu trăiesc aşa cum trăieşte lumea, doar să se bucure de viaţa prezentă, doar să crească familii după trup, ci scopul lor cel mai înalt, obiectivul, străduinţa lor, trebuie să fie umblarea după Spirit şi urmarea îndrumărilor Domnului în toate lucrurile, atât naturale cât şi spirituale. Ei ar trebui să aibă totdeauna în vedere că sunt consacraţi Domnului, morţi cu Cristos în ceea ce priveşte această lume, şi că scopul şi obiectivul lor major în continuare trebuie să fie folosirea vieţii prezente şi a vaselor pământeşti ca sacrificii, în modul cel mai înţelept posibil pentru interesul Noii Creaturi şi al lucrării ei generale de slujire şi slăvire a Domnului, precum şi de zidire a sa şi a altora de aceeaşi credinţă preţioasă în harurile spirituale! Ce important este pentru cei căsătoriţi şi pentru cei necăsătoriţi, precum şi pentru cei care se gândesc la căsătorie, să nu uite că tot ce au este pus pe altar şi că biruinţa lor şi obţinerea lucrurilor glorioase promise pot veni numai prin consumarea sacrificiului; şi, ca atare, toate afacerile vieţii prezente ar trebui orânduite, cât sunt ei în stare, în astfel de manieră cum le-ar sluji mai bine pentru bunăstarea lor spirituală, pentru bunăstarea fraţilor şi pentru gloria Capului nostru!</p>" +
  "<p style='text-align: center; font-weight: bold;'>El Ştie</p>"+
  "<p style='text-align: center; margin: 0;'>El calea mea o ştie --</p>"+
  "<p style='text-align: center; margin: 0;'>Şi-atunci, ce dacă-i neguroasă,</p>"+
  "<p style='text-align: center; margin: 0;'>Ori aspră, ori încercuită --</p>"+
  "<p style='text-align: center; margin: 0;'>Toiagul Lui mă mângâie.<br/><br/></p>"+
  "<p style='text-align: center; margin: 0;'>Şi dragostea-I de Şi-ar reţine,</p>"+
  "<p style='text-align: center; margin: 0;'>Vreun lucru ce atâta de-aproape, </p>"+
  "<p style='text-align: center; margin: 0;'>drag şi dulce mie-mi pare,</p>"+
  "<p style='text-align: center; margin: 0;'>Eu l-aş lua şi cu smerire</p>"+
  "<p style='text-align: center; margin: 0;'>L-aş pune la ale Lui picioare.<br/><br/></p>"+
  "<p style='text-align: center; margin: 0;'>Ce dulce-i gândul că El ştie</p>"+
  "<p style='text-align: center; margin: 0;'>Şi-I pasă şi mâna mi-o ţine --</p>"+
  "<p style='text-align: center; margin: 0;'>Şi sigur mă-ndrumă până ajung</p>"+
  "<p style='text-align: center; margin: 0;'>În Patria Cerească în fine.</p>"+
  "</div>",
];

export default StudyResource;
